<template>
  <div style="display: flex">
    <div class="intro-text" style="min-height:calc(100vh - 60px);width: 155px;background-color: #ecedf1;flex-shrink: 0;">
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none" :default-active="$route.path">
        <el-menu-item index="/realData" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">实时数据表</span>
        </el-menu-item>
        <el-menu-item index="/NumericValue" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">数值表表</span>
        </el-menu-item>
      </el-menu>
      <div style="padding-top: 160px">
        <img src="../../assets/金色时代.png" height="234" width="153"/>
      </div>
    </div>
    <!--    主体区域-->
    <div style="padding: 20px 0 0 20px;width: 100%">
      <div style="padding-bottom: 20px">
        <el-input style="width: 300px;margin-right: 10px" placeholder="请输入搜索内容" v-model="searchText"></el-input>
        <el-button type="primary" @click="load(1)">查询</el-button>
        <el-button type="info" @click="reset">重置</el-button>
        <el-button type="info" plain @click="exportData">批量导出</el-button>
<!--        <el-button type="info" plain @click="compareAndShowResult">对比判断</el-button>-->
      </div>
      <el-table :data="tableData" stripe :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="ID" prop="id" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="数值表编号" prop="valueNum" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="数值表名称" prop="valueName"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="数值表类型" prop="type"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="所属设备" prop="owningDevice" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="所属部门" prop="department" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="真实数值" prop="checkResult" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="判断结果" prop="result" align="center" :show-overflow-tooltip="true"></el-table-column>
      </el-table>
      <div style="margin: 10px 0">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: "NumericValue",
  data() {
    return {
      searchText: '',
      tableData: [],  // 所有的数据
      pageNum: 1,   // 当前的页码
      pageSize: 10,  // 每页显示的个数
      valueName: '',
      total: 0,
      fromVisible: false,
      user: JSON.parse(localStorage.getItem('honey-user') || '{}'),
      form: {},
      rules: {
        account: [
          { required: true, message: '请输入账号', trigger: 'blur' },
        ]
      },
      ids: []
    }
  },
  created() {
    this.load()
  },
  methods: {
    exportData(){   //批量导出数据
      if (!this.ids.length){  //没有选择行的时候，全部导出，或者根据我的搜索条件导出
        window.open(this.$baseUrl + '/numericValue/export?token=' + this.user.token)
      } else {   //[1,2,3] => '1,2,3'
        let idStr = this.ids.join(',')
        window.open(this.$baseUrl + '/numericValue/export?token=' + this.user.token + '&ids=' + idStr)
      }

    },
    compareAndShowResult() { // 新增方法，比较并显示结果
      this.$request.get('/numericValue/compare').then(res => {
        this.tableData = res.data
      })
    },
    delBatch() {
      if (!this.ids.length) {
        this.$message.warning('请选择数据')
        return
      }
      this.$confirm('您确认批量删除这些数据吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/numericValue/delete/batch', { data: this.ids }).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleSelectionChange(rows) {   // 当前选中的所有的行数据
      this.ids = rows.map(v => v.id)
    },
    del(id) {
      this.$confirm('您确认删除吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/numericValue/delete/' + id).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleEdit(row) {   // 编辑数据
      this.form = JSON.parse(JSON.stringify(row))  // 给form对象赋值  注意要深拷贝数据
      this.fromVisible = true   // 打开弹窗
    },
    handleAdd() {   // 新增数据
      this.form = {}  // 新增数据的时候清空数据
      this.fromVisible = true   // 打开弹窗
    },
    save() {   // 保存按钮触发的逻辑  它会触发新增或者更新
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.$request({
            url: this.form.id ? '/numericValue/update': '/numericValue/add',
            method: this.form.id ? 'PUT' : 'POST',
            data: this.form
          }).then(res => {
            if (res.code === '200') {  // 表示成功保存
              this.$message.success('保存成功')
              this.load(1)
              this.fromVisible = false
            } else {
              this.$message.error(res.msg)  // 弹出错误的信息
            }
          })
        }
      })
    },
    reset() {
      this.searchText = '';
      this.load(1);
    },
    load(pageNum) {  // 分页查询
      if (pageNum)  this.pageNum = pageNum
      this.$request.get('/numericValue/selectByPage', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          valueName: this.valueName,
          searchText: this.searchText // 直接传递 searchText 参数
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
      })
    },
    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },
    handleAvatarSuccess(response, file, fileList) {
      // 把user的头像属性换成上传的图片的链接
      this.form.avatar = response.data
    },
  }
}
</script>

<style scoped>

</style>